  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10em 15em 0 15em;
  border-color: transparent #fff;
}
.envelope .paper.front:after {
  content: "";
  display: block;
  width: 30em;
  height: 8em;
  background: #fff;
  margin-top: -1px;
}
